    position: relative;
    z-index: 1;
    transition: all 0.3s ease-out;
  }
  .services-flex{
      display: flex;
      flex-direction: row;
        justify-content: center;
      flex-wrap: wrap;

  }
  .circle:after {
    content: "";
    width: 118px;
    height: 118px;
    display: block;
    position: absolute;
    background: var(--bg-color);
    border-radius: 50%;
    top: 7px;
    left: 7px;
    transition: opacity 0.3s ease-out;
  }
  
  .circle svg {
    z-index: 10000;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  
  .overlay {
    width: 118px;
    position: absolute; 
    height: 118px;
    border-radius: 50%;
    background: var(--bg-color);
    top: 70px;
    left: 50px;
    z-index: 0;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  }
  @media only screen 
and (min-width :1050px) {
    .align-services{
        position: absolute;
        top: 200vh;
    }
}

@media only screen 
and  (max-width:1050px) {
    .align-services{
        position: absolute;
        top: 290vh;
    }
}
